import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { changePassword, sendEmail } from "../../actions";
import history from "../../history";

class ChangePassword extends React.Component {

  onSubmit = formValues => {
    this.props.changePassword(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field`;

    if (input.name === "newpassword" ) {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" />
        </div>
      );
    } else {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" />
        </div>
      );
    }
  };

  sendEmail = formValues => {
    this.props.sendEmail(formValues);
  };

  checkUser = () => {
    return (
      <div className="column form-container">
        <form
          onSubmit={this.props.handleSubmit(this.sendEmail)}
          className="ui form sendmail"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Nazwa użytkownika"
          />

          <button className="ui button">Wyślij maila</button>
        </form>

        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form changepassword"
        >
          <Field
            name="code"
            component={this.renderInput}
            label="Kod z wiadomości email"
          />
          <Field
            name="newpassword"
            component={this.renderInput}
            label="Wprowadź nowe hasło"
          />

          <button className="ui button">Zmień hasło</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      history.push("/");
      return <div>Zalogowany</div>;
    } else {
      return <div id="changePassword">{this.checkUser()}</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};


const formWrapped = reduxForm({
  form: "changepassword"
})(ChangePassword);

export default connect(
  mapStateToProps,
  { changePassword, sendEmail }
)(formWrapped);